import Navbar from "../components/Navbar";


const About = () => (
    <div>
        <Navbar />
        <h1 style={navStyle}>Gerilimsiz</h1>
        <p style={linkStyle}>Gerilimsiz is the stress reduction App that would help you schedule and manage your workflow by adding and deleting tasks todo, thereby reducing workload stress.</p>
      <h3 style={linkStyle}>About App</h3>
    </div> 
);

const linkStyle = {
    textDecoration: 'none',
    weight: 'bold',
    color: '#000',
}

const navStyle = {
    fontstyle: 'italic',
    margin: '0',
    textDecoration: 'none',  
    background: '#3367d6',
    textAlign: 'center',
    padding: '40px',
    color: '#fff'
}

export default About;